import React, { useEffect } from "react";
import { HashRouter as Router, Route, Routes, Link } from "react-router-dom";
import About from "./aboutme";
import Contact from "./contact";
import Projects from "./projects";
import Homepage from "./homepage";
import "../index.css";

const linkedin = "https://www.linkedin.com/in/thanusun";
const github = "https://www.github.com/coolmanbat2";
const instagram = "https://www.instagram.com/ryekg";
const RedirectPage = (props) => {
  console.log("Hello");
  useEffect(() => {
    window.location.replace(props.link);
  }, [props.link]);
  return (
    <div
      className="mx-auto text-white"
      style={{
        margin: "auto",
        display: "block",
        textAlign: "center",
        paddingTop: "17rem",
      }}
    >
      <h1> Redirecting </h1>
    </div>
  );
};

const Navigation = () => (
  <Router>
    <Routes>
      <Route
        path="/"
        element={
          <Homepage
            name="Thanusun Thayaparan"
            occupation="Software Developer"
          />
        }
      />
      <Route path="/aboutme" element={<About />} />
      <Route path="/contact" element={<Contact />} />
      <Route path="/projects" element={<Projects />} />
      <Route path="/github" element={<RedirectPage link={github} />} />
      <Route path="/linkedin" element={<RedirectPage link={linkedin} />} />
      <Route path="/instagram" element={<RedirectPage link={instagram} />} />
    </Routes>
  </Router>
);

export default Navigation;
