import React from "react";
import {Link} from 'react-router-dom'

import profilePicture from '../static/profilePic.png'
import '../index.css'
import '../index.js'
import { useSpring, animated } from 'react-spring'
import { config } from "@fortawesome/fontawesome-svg-core";

function Homepage(props) {
const homepageanimation = useSpring({
    to: { opacity: 1 },
    from: { opacity: 0 },
    delay: 300,
    reset: false,
    config: config.molasses,
});
  return (
    <div className={props.className}>
      <animated.div style={homepageanimation} className="d-flex flex-column align-items-center center">
        <img 
          className="profile_pic"
          src={profilePicture} 
          alt="profilePic"/>
        <h3 className="text-center text-white font-weight-normal mt-4"> {props.name} </h3>
        <h5 className="text-center text-white font-weight-normal"> {props.occupation} </h5>
        <div className="buttons">
          <Link to='/aboutme'>
            <button type="button" className="btn mr-2 btn-outline-light">About Me</button>
          </Link>
          <Link to='/contact'>
            <button type="button" className="btn mr-2 btn-outline-light">Contact</button>
          </Link>
          <Link to='/projects'>
            <button type="button" className="btn btn-outline-light">Projects</button>
          </Link>
        </div>
      </animated.div>
    </div>
  )
}

export default Homepage;
